const app = getApp()
import pickerAddress from '../componets/wangding-pickerAddress.vue';
export default {
	data() {
		return {
			address: {
				area: '',
				details: '',
				mobile: '',
				name: '',
				is_default: ''
			},
			isFalse: false
			// txt: ''
		}
	},
	components: {
		pickerAddress
	},
	onLoad(option) {
		console.log(option)
		if(option.type == 2){
			this.address = JSON.parse(option.obj);
			this.address.mobile = this.address.mobile.toString() 
			if(this.address.is_default == 1){
				this.isFalse = true
			}else{
				this.isFalse = false
			}
			this.url = 'user/editAddress'
		}else{
			this.url = 'user/createAddress'
		}
		
	},
	methods: {
		inputValue(e){
			let address = this.address
			let name = e.currentTarget.dataset.name
			let value = e.detail.value
			address[name] = value
			this.address = address
			console.log(e)
		},
		// 省市区联动
		goAddress(e){
			console.log(e)
			this.address.area = e.data.join('');
		},
		getSwitch(e){
			console.log(e)
			if(e.detail.value == true){
				this.address.is_default = 1
			}else{
				this.address.is_default = 0
			}
		},
		getSubmint(){
			console.log(this.address.mobile)
			if(this.address.name == ''){
				uni.showToast({
					title: '请输入收件人姓名',
					icon: 'none'
				})
				return false
			}
			if(this.address.mobile == ''){
				uni.showToast({
					title: '请输入收件人电话',
					icon: 'none'
				})
				return false
			}
			if(this.address.mobile.length != 11){
				uni.showToast({
					title: '请输入正确电话',
					icon: 'none'
				})
				return false
			}
			if(this.address.area == ''){
				uni.showToast({
					title: '请选择地区',
					icon: 'none'
				})
				return false
			}
			if(this.address.details == ''){
				uni.showToast({
					title: '请输入详细地址',
					icon: 'none'
				})
				return false
			}
			uni.showLoading({
				title: '保存中...',
				icon: 'none'
			})
			uni.request({
				url: `${this.$helper.requestUrl}${this.url}`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: this.address,
				success: res => {
					uni.hideLoading();
					res = this.$helper.null2str(res.data)
					console.log(res,'++++++++')
					if(res.status_code == 'ok'){
						uni.showToast({
							title: res.message,
							icon: 'none',
							duration: 2000
						})
						uni.setStorageSync('address',this.address);
						setTimeout(() => {
							uni.navigateBack({
								delta:1
							})
						}, 2000)
					}else{
						uni.showToast({
							title: res.message,
							icon: 'none'
						})
					}
				}
			})
		}

		
	}
}
